import React from 'react';
import {
  AudioOutlined,
  AudioMutedOutlined
} from '@ant-design/icons';
import SpeechRecognition, { useSpeechRecognition } from 'react-speech-recognition';
import './index.less';


interface VoiceControlBtnProps {
  temperature: number;
  setTemperature: (temperature: number) => void;
}

function VoiceControlBtn({
  setTemperature,
  temperature
}: VoiceControlBtnProps) {
  const commands = [
    {
      command: 'increase temperature',
      callback: () => setTemperature(Math.min(40, temperature + 1))
    },
    {
      command: 'decrease temperature',
      callback: () => setTemperature(Math.max(20, temperature - 1))
    },
    {
      command: 'increase temperature by *',
      callback: (val: string) => {
        const t = parseInt(val, 10);
        if (isNaN(t)) return;
        setTemperature(Math.min(40, temperature + t));
      }
    },
    {
      command: 'decrease temperature by *',
      callback: (val: string) => {
        const t = parseInt(val, 10);
        if (isNaN(t)) return;
        setTemperature(Math.max(20, temperature - t));
      }
    },
    {
      command: 'stop',
      callback: () => SpeechRecognition.stopListening()
    },
  ];

  const { listening } = useSpeechRecognition({ commands });

  if (!SpeechRecognition.browserSupportsSpeechRecognition()) {
    return null;
  }

  return (
    <div className='voice-btn-wapper'>
      {!listening
        ? (
          <button
            type='button'
            onClick={(): void => {
              SpeechRecognition.startListening({ continuous: true });
            }}
          >
            <AudioMutedOutlined />
          </button>
        )
        : (
          <button
            type='button'
            className='active'
            onClick={SpeechRecognition.stopListening}
          >
            <AudioOutlined />
          </button>
        )}
    </div>
  );
}

export default VoiceControlBtn;
