function ProfilePage() {
  const carouseEl = useRef(null);

  return (
    <div className='profile-page-wrapper'>
      <ProfileSider 
        goTo={(index: number) => {
          (carouseEl as React.MutableRefObject<any>).current.goTo(index);
        }}
      />
      <Carousel
        infinite={false}
        dotPosition='bottom'
        ref={carouseEl}
        swipe={false}
      >
        <ProfileSection />
        <ChildProfileSection />
      </Carousel>
    </div>
  );
}

export default ProfilePage;
